import {post,get} from './axios'
const BASE_URL = ''

//用户注册
export const toRegister = (username,password,status)=>post(BASE_URL+'/register',{username,password,status});

//用户登录
export const toLogin = (username,password)=>post(BASE_URL+'/login',{username,password});


//文章发布
export const toRelease = (title,content,label,mold,currentime,author,image)=>post(BASE_URL+'/release',{title,content,label,mold,currentime,author,image});


//获取所有文章
export const getAllContent =()=>get(BASE_URL+'/getallcontent');


//寻找对应的文章
export const findId = (id)=>post(BASE_URL+'/findid',{id});

//获取个人文章
export const getOwnContent =(author)=>post(BASE_URL+'/getowncontent',{author});

//修改个人资料

export const modifyingData = (username,birthday,location,information,realname, job,sex)=>post(BASE_URL+'/modifyingdata',{username,birthday,location,information,realname, job,sex});


//获取个人信息
export const getInfo =(username)=>post(BASE_URL+'/getinfo',{username});

//上传图片
export const submitImage =(username,image)=>post(BASE_URL+'/submitImage',{username,image});


//获取图片
export const getImage =(username)=>post(BASE_URL+'/getimage',{username});

//删除文章
export const delArticle =(id)=>post(BASE_URL+'/delarticle',{id});

//编辑文章
export const editArticle =(id,title,content,label,mold,currentime)=>post(BASE_URL+'/editarticle',{id,title,content,label,mold,currentime});

//修改密码
export const changePassword =(id,changepassword)=>post(BASE_URL+'/changepassword',{id,changepassword});
//阅读数
export const readConut =(count,id)=>post(BASE_URL+'/readconut',{count,id});

//评论发布
export const commitTo =(img,name,id,comment,tousername,status,commentime)=>post(BASE_URL+'/commit',{img,name,id,comment,tousername,status,commentime});
//相对应的评论
export const comments =(blogid)=>post(BASE_URL+'/getcommit',{blogid});
//获取我的评论
export const myComments =(tousername)=>post(BASE_URL+'/getmycomment',{tousername});
//修改评论状态
export const modifyStatus =(id,status)=>post(BASE_URL+'/modifyStatus',{id,status});
//获取用户卡片
export const getUserCard =(bloguser)=>post(BASE_URL+'/getUserCard',{bloguser});
//关注用户
export const careUser =(bloguser,carebloguser,carebloguserimg,carebloguserstate,careblogusersex,carestatus)=>post(BASE_URL+'/careUser',{bloguser,carebloguser,carebloguserimg,carebloguserstate,careblogusersex,carestatus});
//获取用户关注列表
export const getUserList =(bloguser)=>post(BASE_URL+'/getUserList',{bloguser});
//取消用户关注
export const cancleCare =(carebloguser)=>post(BASE_URL+'/cancleCare',{carebloguser});

//更新用户状态
export const modifyuserstatus =(id,status)=>post(BASE_URL+'/modifyuserstatus',{id,status});


